.page-body {
  font-family: "Roboto Condensed", "Arial", sans-serif;

  display: flex;
  overflow-x: hidden;
  flex-direction: column;
  min-width: $mobile-width;
  min-height: 100%;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  transition: 0.2s;
}

.page-body__inner {
  max-width: $desktop-max-content;
  margin: 0 auto;
  padding: 0 20px;

  @media (max-width: $tablet-width-max) {
    padding: 0 45px;
  }

  @media (max-width: $mobile-width-max) {
    padding: 0 15px;
  }
}

.page-body__footer {
  margin-top: auto;
}
